
import {mapState} from 'vuex'
import Loading from 'vux/src/components/loading/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Rater from 'vux/src/components/rater/index.vue'

import alert from './compon.vue'
export default {
data(){
return {
indexSwipe:0,
isLoading:false,
ifstatus:false,
score:5,
pullupconfig:{
content: '上拉刷新',
height: 40,
autoRefresh: false,//刷新当钱页面
downContent: '<i>释放刷新</i>',
upContent: '<span style="margin-top: 10px;display: inline-block;">上拉加载更多</span>',
loadingContent: '<i class="weui-loading weui-icon_toast" style="margin: 0;"></i>',
clsPrefix: 'xs-plugin-pulldown-'
}
}
},
components:{
Loading,
LoadMore,
Group ,
Cell,
Clocker,
alert,
Spinner,
Qrcode,
Swiper,
Rater,
SwiperItem
},
mounted(){
var _this = this;
var obj = {
num:0,
fn:function(){
setTimeout(()=>{
_this.$refs.scrollerBottom.reset()
_this.$store.commit('updateLoadingStatus', {isLoading: false});
})
}
}
this.$store.dispatch('getHead');
this.$store.commit('updateLoadingStatus', {isLoading: true});
this.$store.dispatch('getMainlist',obj);
},
computed:{
...mapState({
headlist:(state)=>{
return state.index.headList
},
mainList:(state)=>{
return state.index.mainList
}
}),
swipeLens(){
return this.headlist.length/8
}
},
activated(){

},
beforeRouteEnter (to, from, next) {
next(vm=>{
console.log(vm.score)
vm.$store.commit('changetit','首页');
vm.$store.commit('changeHead',true);
vm.$store.commit('changeshowBar',false);
vm.$store.commit('changeTaber',true);
})
},
beforeRouteLeave (to, from, next) {
this.score = '123'
next()
},
methods:{
jumpdefualt(){
this.$router.push('/hellodefault');
},
action(){
this.$vux.loading.show({
text: '加载中'
});
setTimeout(()=>{
this.$vux.loading.hide()
},1000)
},
onIndexChange(){
console.log(123)
},
upmore(){
var _this = this;
var obj = {
num:20,
fn:function(){
_this.ifstatus = true;
setTimeout(()=>{
_this.$refs.scrollerBottom.donePullup();
_this.$refs.scrollerBottom.reset();
_this.$refs.scrollerBottom.disablePullup();
})
}
}
this.$store.dispatch('getMainlist',obj);
}
}
}



