
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default {
components:{
XButton,
Group,
XInput
},
data(){
return {
items: [],
searchList:[],
adress:'',
inpVal:'',
close:false,
searchCon:false,
searchState:true,
}
},
mounted(){
this.searchList = JSON.parse(localStorage.getItem('searchList'))||[];
},
watch:{
inpVal(n,o){
if(n){
this.close = true
}else{
this.close = false;
this.searchCon = false;
this.searchState = true;
}
}
},
beforeRouteEnter (to, from, next) {
next(vm=>{
vm.$store.commit('changetit','搜索');
vm.$store.commit('changeshowBar',true);
vm.$store.commit('changeTaber',true);
})
},
beforeRouteLeave(to, from, next){
next();
},
methods:{
closee(){
this.inpVal = '';
this.searchCon = false;
this.searchState = true;
},
search(){
this.$store.commit('updateLoadingStatus', {isLoading: true});
setTimeout(()=>{
this.$store.commit('updateLoadingStatus', {isLoading: false});
if(!this.inpVal) return;
this.searchCon = true;
this.searchState = false;
if(this.searchList.indexOf(this.inpVal)>-1) return ;
this.searchList.push(this.inpVal);
localStorage.setItem('searchList',JSON.stringify(this.searchList));
},1000)
},
clearInp(){
this.searchList = [];
localStorage.setItem('searchList','')
},
deletItem(item){
var index = this.searchList.indexOf(item);
this.searchList.splice(index,1);
localStorage.setItem('searchList',JSON.stringify(this.searchList))
},
historyInp(item){
this.inpVal = item;
this.search();
}
}
}



